const demo = [{
	userName: "昵称",
	userPic: "/static/demo/userpic/5.jpg",
	newsTime: "2019-12-30 下午4点30",
	isFollow: false,
	title: "我是标题",
	titlePic: "/static/demo/datapic/3.jpg",
	support: {
		type: "",
		support_count: 1,
		unsupport_count: 2
	},
	comment_count: 2,
	share_count: 2
},{
	userName: "昵称",
	userPic: "/static/demo/userpic/1.jpg",
	newsTime: "2019-12-30 下午4点30",
	isFollow: false,
	title: "我是标题",
	titlePic: "/static/demo/datapic/1.jpg",
	support: {
		type: "",
		support_count: 1,
		unsupport_count: 2
	},
	comment_count: 2,
	share_count: 2
}];
const demo1 = [{
	userName: "昵称",
	userPic: "/static/demo/userpic/1.jpg",
	newsTime: "2019-12-30 下午4点30",
	isFollow: false,
	title: "我是标题",
	titlePic: "/static/demo/datapic/2.jpg",
	support: {
		type: "",
		support_count: 1,
		unsupport_count: 2
	},
	comment_count: 2,
	share_count: 2
},{
	userName: "昵称",
	userPic: "/static/demo/userpic/5.jpg",
	newsTime: "2019-12-30 下午4点30",
	isFollow: false,
	title: "我是标题",
	titlePic: "/static/demo/datapic/3.jpg",
	support: {
		type: "",
		support_count: 1,
		unsupport_count: 2
	},
	comment_count: 2,
	share_count: 2
}];
import commonList from '@/components/common/common-list.vue'
import loadMore from '@/components/common/load-more';
export default {
	components: {
		commonList,
		loadMore
	},
	data() {
		return {
			scrollH: 600,
			tabBars: [{
				name: '关注'
			}, {
				name: '推荐'
			}, {
				name: '体育'
			}, {
				name: '热点'
			}, {
				name: '财经'
			}, {
				name: '娱乐'
			}, {
				name: '军事'
			}, {
				name: '历史'
			}, {
				name: '本地'
			}],
			tabIndex: 0,
			scrollInto: "",
			newsList: [],
		}
	},
	onLoad() {
		uni.getSystemInfo({
			success: res => {
				this.scrollH = res.windowHeight - uni.upx2px(100) - 50;
			}
		})
		// 跟据选项生产列表
		this.getData();
	},
	// 监听点击原生标题栏搜索事件
	onNavigationBarSearchInputClicked() {
		uni.navigateTo({
			url: '../search/search',
		});
	},
	// 监听导航按钮事件
	onNavigationBarButtonTap(e) {
		uni.navigateTo({
			url: '../add-input/add-input',
		});
	},
	methods: {
		// 获取数据
		getData() {
			var arr = [];
			for (let i = 0; i < this.tabBars.length; i++) {
				// 生产列表模板
				let obj = {
					loadmore: "上拉加载更多", //1.上拉加载更多 2.加载中... 3.没有更多了
					list: []
				}
				if (i < 2) {
					if(i == 1){
						obj.list = demo;
					}else{
						obj.list = demo1;
					}
				}
				arr.push(obj);
			}
			this.newsList = arr;
		},
		// 下拉加载更多
		loadmore(index) {
			// 拿到当前列表数据
			let item = this.newsList[index];
			if (item.loadmore !== "上拉加载更多") return
			// 修改当前列表加载状态
			item.loadmore = "加载中...";
			//模拟加载请求
			setTimeout(() => {
				// 加载数据
				item.list = [...item.list, ...item.list]
				this.newsList[index].loadmore = "上拉加载更多";
			}, 2000)
		},
		// 切换选项
		changeTab(index) {
			if (index === this.tabIndex) {
				return
			}
			this.tabIndex = index;
			// 滚动到指定题目
			this.scrollInto = "tab" + index
		},
		// 监听滑动
		onChangeTab(e) {
			this.changeTab(e.detail.current);
		},
		// 关注
		follow(e) {
			this.newsList[this.tabIndex].list[e].isFollow = true;
			uni.showToast({
				title: '关注成功'
			});
		},
		// 顶踩操作
		doSupport(e) {
			// 拿到当前对象
			const list = this.newsList[this.tabIndex].list[e.index];
			let msg = e.type === "support" ? '顶' : '踩'
			if (list.support.type === "") { // 之前没有操作过
				list.support[e.type + '_count']++;
			} else if (list.support.type === "support" && e.type === "unsupport") { //之前顶过
				// 顶--
				list.support.support_count--;
				// 踩++ 
				list.support.unsupport_count++;
			} else if (list.support.type === "unsupport" && e.type === "support") { //之前踩过
				// 顶++
				list.support.support_count++;
				// 踩--
				list.support.unsupport_count--;
			}
			list.support.type = e.type;
			uni.showToast({
				title: msg + "成功"
			});
		}
	}
}
